// SECTION :: START

.footer {
	background-color: #efedeb;

	&__wrapper {
		display: flex;
		align-items: flex-end;
		padding-bottom: 0;
		padding-left: 0;
		padding-right: 0;
	}

	&__content {
		width: 100%;
	}

	&__social {
		display: flex;
		flex-direction: column;
		width: 100%;
		max-width: 300px;
		padding-bottom: 40px;
		padding-left: 40px;
		margin-left: auto;
		margin-right: auto;

		a {
			display: flex;
			align-items: center;

			&:not(:last-of-type) {
				padding-bottom: 15px;
			}
			
			img {
				display: block;
				width: 100%;
				max-width: 40px;
				height: auto;
				margin-right: 25px;
				@include adaptive(767) {
					width: 100%;
					max-width: 40px;
				}
			}

			span {
				font-size: 18px;
				color: #505050;
			}
		}
	}

	&__title {
		font-size: 30px;
		color: #505050;
		text-align: center;
		@include adaptive(767) {
			padding-bottom: 20px;
			font-size: 22px;
		}
	}

	&__subtitle {
		text-align: center;
		font-size: 35px;
		color: #d68162;
		line-height: 3;
		font-family: 'StylishClassyFont', sans-serif;
		@include adaptive(767) {
			font-size: 30px;
		}
	}

	&__line {
		width: 130px;
		height: 2px;
		background-color: #c0bab1;
		margin: 30px auto;
	}

	&__menu {
		display: flex;
		flex-direction: column;
		padding-left: 95px;
		padding-right: 95px;
		padding-bottom: 30px;
		@include adaptive(767) {
			padding-left: 15px;
			padding-right: 15px;
		}

		a {
			font-size: 18px;
			color: rgb(80, 80, 80);
			@include adaptive(767) {
				text-align: center;
			}

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__copyright {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 50px;
		background-color: #eec9b7;

		p {
			font-size: 18px;
			color: #d68162;
			@include adaptive(767) {
				font-size: 14px;
				text-align: center;
			}
		}
	}
}

// SECTION :: END
